@import url(//spoqa.github.io/spoqa-han-sans/css/SpoqaHanSansNeo.css);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@400;500;700;900&Roboto:wght@500;700;900&display=swap);
body{background-color:#222831;color:white;font-family:'Spoqa Han Sans Neo', 'Roboto', sans-serif;overflow-x:hidden}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.container-navbar{background-color:#393e46}.container-navbar .nav-link{color:white}.container-navbar .nav-link:hover{color:white}.container-main{margin-top:30px;height:1200px}.container-main .container-profile{width:100%;text-align:center;padding:0 40px}.container-main .container-profile>div{padding:20px 10px;border:3px dashed white;border-radius:10px}.container-main .container-content{padding:0 40px}.image-profile{width:60%}.image-toggle-profile{width:100px;position:fixed;right:10px;bottom:10px;z-index:1}.container-profile-mini{background:#393e46;padding:20px;position:fixed;right:20px;bottom:150px;z-index:2}.arrow_box{width:200px;border:4px solid #ffd369;border-radius:5px}.arrow_box:after,.arrow_box:before{top:100%;left:80%;border:solid transparent;content:"";height:0;width:0;position:absolute;pointer-events:none}.arrow_box:after{border-color:rgba(57,62,70,0);border-top-color:#393e46;border-width:30px;margin-left:-30px}.arrow_box:before{border-color:rgba(255,211,105,0);border-top-color:#ffd369;border-width:36px;margin-left:-36px}

